import React from "react";
import {Link} from 'react-router-dom';
import {addArticleToCart} from "../actions";
import {connect} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';

class HomeFeat4 extends React.Component {

    render() {
        return this.props.articles !== undefined ? (
		<section id="mvp_home_feat1_widget-4" class="mvp-widget-home left relative mvp_home_feat1_widget">
			<div class="mvp-main-box">
				<div class="mvp-widget-home-head">
					<h4 class="mvp-widget-home-title">
						<span class="mvp-widget-home-title">Sports</span>
					</h4>
				</div>
				<div class="mvp-widget-feat1-wrap left relative">
					<div class="mvp-widget-feat1-cont left relative">
					{this.props.mainarticle.map((article)=>{
									var age;
									var date1 = new Date();
									var date2 = new Date(article.TimeStamp); // <br> means line break in html
									const diffTime = date1 - date2
									const diffYears = Math.floor(diffTime / (1000 * 60 * 60 * 24 * 30 * 12));
									if (diffYears!==0){
										age = diffYears + 'years ago';
									}else{
									const diffMonths= Math.floor(diffTime / (1000 * 60 * 60 * 24 * 30));
									if (diffMonths!==0){
										age = diffMonths + 'months ago';
									}else{
									const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
									if (diffDays!==0){
										age = diffDays + 'days ago';
									}else{
										age = Math.floor(diffTime / (1000 * 60 * 60 )) + 'hours ago';
									}}};
									return(
						<a href={article.Link} rel="bookmark">
							<div class="mvp-widget-feat1-top-story left relative">
								<div class="mvp-widget-feat1-top-img left relative">
									<img width="590" height="354" src={article.Imagelink} class="mvp-reg-img lazy wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset-590x354.jpg 590w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset-600x360.jpg 600w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset-300x180.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset-768x461.jpg 768w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset-400x240.jpg 400w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset.jpg 1000w" sizes="(max-width: 590px) 100vw, 590px"/>												
									<img width="400" height="240" src={article.Imagelink} class="mvp-mob-img lazy wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset-400x240.jpg 400w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset-600x360.jpg 600w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset-300x180.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset-768x461.jpg 768w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset-590x354.jpg 590w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/vr-headset.jpg 1000w" sizes="(max-width: 400px) 100vw, 400px"/>																																
								</div>
								<div class="mvp-widget-feat1-top-text left relative">
									<div class="mvp-cat-date-wrap left relative">
									<span class="mvp-cd-cat left relative">{article.Newspaper}</span><span class="mvp-cd-date left relative">{age}</span>
									</div>
									<h2>{article.Title}</h2>
								</div>
							</div>
						</a>
						)})}
					</div>
					<div class="mvp-widget-feat1-cont left relative">
					{this.props.articles.map((article)=>{
									var age;
									var date1 = new Date();
									var date2 = new Date(article.TimeStamp); // <br> means line break in html
									const diffTime = date1 - date2
									const diffYears = Math.floor(diffTime / (1000 * 60 * 60 * 24 * 30 * 12));
									if (diffYears!==0){
										age = diffYears + 'years ago';
									}else{
									const diffMonths= Math.floor(diffTime / (1000 * 60 * 60 * 24 * 30));
									if (diffMonths!==0){
										age = diffMonths + 'months ago';
									}else{
									const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
									if (diffDays!==0){
										age = diffDays + 'days ago';
									}else{
										age = Math.floor(diffTime / (1000 * 60 * 60 )) + 'hours ago';
									}}};
									return(
						<a href={article.Link} rel="bookmark">
							<div class="mvp-widget-feat1-bot-story left relative">
								<div class="mvp-widget-feat1-bot-img left relative">
									<img width="400" height="240" src={article.Imagelink} class="mvp-reg-img lazy wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/business-woman-400x240.jpg 400w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/business-woman-600x360.jpg 600w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/business-woman-300x180.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/business-woman-768x461.jpg 768w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/business-woman-590x354.jpg 590w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/business-woman.jpg 1000w" sizes="(max-width: 400px) 100vw, 400px"/>												
									<img width="80" height="80" src={article.Imagelink} class="mvp-mob-img lazy wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/business-woman-80x80.jpg 80w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/business-woman-300x300.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/business-woman-100x100.jpg 100w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/business-woman-150x150.jpg 150w" sizes="(max-width: 80px) 100vw, 80px"/>																																
								</div>
								<div class="mvp-widget-feat1-bot-text left relative">
									<div class="mvp-cat-date-wrap left relative">
										<span class="mvp-cd-cat left relative">{article.Newspaper}</span><span class="mvp-cd-date left relative">{age}</span>
									</div>
									<h2>{article.Title}</h2>
								</div>
							</div>
						</a>
						)})}
						<Link to={'/news/sport'}>
								<div class="mvp-widget-feat2-side-more-but left relative">
								<span class="mvp-widget-feat2-side-more">More Sports</span><i class="fa fa-long-arrow-right" aria-hidden="true"></i>
								</div>
						</Link>
					</div>
				</div>
			</div>
		</section>
        ):(<div style={{marginTop:'350px', height:'700px'}}><center><CircularProgress disableShrink /></center></div>)
    }
}
const mapStateToProps = (state, props) =>  {
	const allarticles =  state.article.articles.filter(article => article.Category === 'sports');
	const mainarticle =  allarticles.filter(article => article.Place === 'top');
	const articles =  allarticles.filter(article => article.Place === 'down');

	return {
		articles, mainarticle
	}
}
export default connect(mapStateToProps, null)(HomeFeat4);
