import React from "react";
import {Link} from 'react-router-dom';
import {addArticleToCart} from "../actions";
import {connect} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';

class HomeFeat5 extends React.Component {

    render() {
        return this.props.allarticles !== undefined ? (
			<section class="mvp-widget-home left relative">
				<div class="mvp-widget-dark-wrap left relative">
					<div class="mvp-main-box">
						<div class="mvp-widget-home-head">
							<h4 class="mvp-widget-home-title"><span class="mvp-widget-home-title">Entertainement</span></h4>
						</div>
						<div class="mvp-widget-dark-main left relative">
							<div class="mvp-widget-dark-left left relative">
							{this.props.article.map((article)=>{
										var age;
										var date1 = new Date();
										var date2 = new Date(article.TimeStamp); // <br> means line break in html
										const diffTime = date1 - date2
										const diffYears = Math.floor(diffTime / (1000 * 60 * 60 * 24 * 30 * 12));
										if (diffYears!==0){
											age = diffYears + 'years ago';
										}else{
										const diffMonths= Math.floor(diffTime / (1000 * 60 * 60 * 24 * 30));
										if (diffMonths!==0){
											age = diffMonths + 'months ago';
										}else{
										const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
										if (diffDays!==0){
											age = diffDays + 'days ago';
										}else{
											age = Math.floor(diffTime / (1000 * 60 * 60 )) + 'hours ago';
										}}};
										return(
								<a href={article.Link} rel="bookmark">
									<div class="mvp-widget-dark-feat left relative">
										<div class="mvp-widget-dark-feat-img left relative">
											<img width="1000" height="600" src={article.Imagelink} class="mvp-reg-img lazy wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer.jpg 1000w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer-600x360.jpg 600w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer-300x180.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer-768x461.jpg 768w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer-590x354.jpg 590w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer-400x240.jpg 400w" sizes="(max-width: 1000px) 100vw, 1000px"/>												
											<img width="400" height="240" src={article.Imagelink} class="mvp-mob-img lazy wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer-400x240.jpg 400w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer-600x360.jpg 600w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer-300x180.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer-768x461.jpg 768w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer-590x354.jpg 590w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/gamer.jpg 1000w" sizes="(max-width: 400px) 100vw, 400px"/>																																		
											<div class="mvp-vid-box-wrap mvp-vid-marg">
												<i class="fa fa-2 fa-play" aria-hidden="true"></i>
											</div>
										</div>
										<div class="mvp-widget-dark-feat-text left relative">
											<div class="mvp-cat-date-wrap left relative">
												<span class="mvp-cd-cat left relative">{article.Newspaper}</span><span class="mvp-cd-date left relative">{age}</span>
											</div>
											<h2>{article.Title}</h2>
										</div>
									</div>
								</a>
								)})}
							</div>
						<div class="mvp-widget-dark-right left relative">
						{this.props.articles.map((article)=>{
										var age;
										var date1 = new Date();
										var date2 = new Date(article.TimeStamp); // <br> means line break in html
										const diffTime = date1 - date2
										const diffYears = Math.floor(diffTime / (1000 * 60 * 60 * 24 * 30 * 12));
										if (diffYears!==0){
											age = diffYears + 'years ago';
										}else{
										const diffMonths= Math.floor(diffTime / (1000 * 60 * 60 * 24 * 30));
										if (diffMonths!==0){
											age = diffMonths + 'months ago';
										}else{
										const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
										if (diffDays!==0){
											age = diffDays + 'days ago';
										}else{
											age = Math.floor(diffTime / (1000 * 60 * 60 )) + 'hours ago';
										}}};
										return(
												<a href={article.Link} rel="bookmark">
													<div class="mvp-widget-dark-sub left relative">
														<div class="mvp-widget-dark-sub-out right relative">
															<div class="mvp-widget-dark-sub-img left relative">
																<img width="400" height="240" src={article.Imagelink} class="mvp-reg-img lazy wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/superman-400x240.jpg 400w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/superman-600x360.jpg 600w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/superman-300x180.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/superman-768x461.jpg 768w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/superman-590x354.jpg 590w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/superman.jpg 1000w" sizes="(max-width: 400px) 100vw, 400px"/>												
																<img width="80" height="80" src={article.Imagelink} class="mvp-mob-img lazy wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/superman-80x80.jpg 80w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/superman-300x300.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/superman-100x100.jpg 100w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/superman-150x150.jpg 150w" sizes="(max-width: 80px) 100vw, 80px"/>																									
																<div class="mvp-vid-box-wrap mvp-vid-box-small mvp-vid-marg-small">
																	<i class="fa fa-2 fa-play" aria-hidden="true"></i>
																</div>
															</div>
															<div class="mvp-widget-dark-sub-in">
																<div class="mvp-widget-dark-sub-text left relative">
																	<div class="mvp-cat-date-wrap left relative">
																		<span class="mvp-cd-cat left relative">{article.Newspaper}</span><span class="mvp-cd-date left relative">{age}</span>
																	</div>
																	<h2>{article.Title}</h2>
																</div>
															</div>
														</div>
													</div>
												</a>
												)})}
							<Link to={'/news/entertainment'}>
								<div class="mvp-widget-feat2-side-more-but left relative">
									<span class="mvp-widget-feat2-side-more">More Entertainment</span><i class="fa fa-long-arrow-right" aria-hidden="true"></i>
								</div>
							</Link>
						</div>
					</div>
					</div>
				</div>
			</section>
        ):(<div style={{marginTop:'350px', height:'700px'}}><center><CircularProgress disableShrink /></center></div>)
    }
}
const mapStateToProps = (state, props) =>  {

	const allarticles =  state.article.articles.filter(article => article.Category === 'entertainement');
	const article =  allarticles.filter(article => article.Place === 'main');
	const articles =  allarticles.filter(article => article.Place === 'side');
	
	return {
		articles, article, allarticles
	}
}
export default connect(mapStateToProps, null)(HomeFeat5);
