import {receiveArticles} from './../actions/index';
import request from 'superagent'

export const getAllArticles = () => (dispatch) => {
    console.log("onload...")
        request
            .get('/api/get/articles')
            .query({ query: 'Manny' })
            .query({ range: '1..5' })
            .query({ order: 'desc' })
            .set('API-Key', 'foobar')
            .set('Accept', 'application/json')
            .end((err, resp) => {
            if (!err) {
                console.log("success")
                const file =  JSON.parse(resp.text)
                console.log(file)
                dispatch(receiveArticles(file))
            } 
            else {
                console.log("failed")
                console.log(err)
                console.log("end")
                return []
            }
            })
          }

