const mapStateToProps =  (state, props) =>  {

	const allarticles =  state.article.articles.filter(article => article.Category === 'business');
	const mainarticle =  allarticles.filter(article => article.Place === 'main');
	const articler =  allarticles.filter(article => article.Place === 'sidel');
	const articlel =  allarticles.filter(article => article.Place === 'sider');
	
	return {
		articlel, mainarticle, articler
	}
}
export default connect(mapStateToProps, null)(HomeFeat3);