import React from "react";
import {addArticleToCart} from "../actions";
import {connect} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';


class Cat extends React.Component {
    constructor(props){
		super(props);
	}
    render() {
		
        return this.props.Trendarticle !== undefined ? (
				<div>
					<div class="mvp-main-body-max">
							<div id="mvp-post-feat-img-wide" class="left relative">
								<div id="mvp-post-feat-img" class="left relative mvp-post-feat-img-wide2" itemprop="image" itemscope="" itemtype="https://schema.org/ImageObject">
									<img width="1600" height="960" src={this.props.Trendarticle.Imagelink} class="mvp-reg-img wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/woman-curly.jpg 1600w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/woman-curly-600x360.jpg 600w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/woman-curly-300x180.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/woman-curly-768x461.jpg 768w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/woman-curly-1024x614.jpg 1024w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/woman-curly-1000x600.jpg 1000w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/woman-curly-590x354.jpg 590w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/woman-curly-400x240.jpg 400w" sizes="(max-width: 1600px) 100vw, 1600px" />
									<img width="560" height="600" src={this.props.Trendarticle.Imagelink} class="mvp-mob-img wp-post-image" alt="" />
									<meta itemprop="url" content={this.props.Trendarticle.Imagelink} />
									<meta itemprop="width" content="1000" />
									<meta itemprop="height" content="600" />
								</div>				
								<div id="mvp-post-feat-text-wrap" class="left relative">
									<div class="mvp-post-feat-text-main">
										<div class="mvp-post-feat-text left relative">
											<h3 class="mvp-post-cat left relative">
												<a class="mvp-post-cat-link" href={this.props.Trendarticle.Link}>
													<span class="mvp-post-cat left">{this.props.match.params.category}</span>
												</a>
											</h3>
											<h1 class="mvp-post-title mvp-post-title-wide left entry-title" itemprop="headline">{this.props.Trendarticle.Title}</h1>
											{/* <span class="mvp-post-excerpt left">
												<p>At vero eos et accusamus et iusto odio dignissimos ducimus qui blanditiis praesentium voluptatum deleniti atque.</p>
											</span> */}
										</div>
									</div>
								</div>
								<span class="mvp-feat-caption">{this.props.Trendarticle.Newspaper}</span>
							</div>
						</div>
					<div class="paddings">
							<h4 class="mvp-widget-home-title">
								<span class="mvp-widget-home-title">Trending</span>
							</h4>
							<ul class="mvp-post-more-list left relative" style={{paddingBottom:"20px"}}>
								{this.props.articles.map((article)=>{
									var age;
									var date1 = new Date();
									var date2 = new Date(article.TimeStamp); // <br> means line break in html
									const diffTime = date1 - date2
									const diffYears = Math.floor(diffTime / (1000 * 60 * 60 * 24 * 30 * 12));
									if (diffYears!==0){
										age = diffYears + 'years ago';
									}else{
									const diffMonths= Math.floor(diffTime / (1000 * 60 * 60 * 24 * 30));
									if (diffMonths!==0){
										age = diffMonths + 'months ago';
									}else{
									const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
									if (diffDays!==0){
										age = diffDays + 'days ago';
									}else{
										age = Math.floor(diffTime / (1000 * 60 * 60 )) + 'hours ago';
									}}};
									return(
										<a href={article.Link} rel="bookmark">
										<li>
											<div class="mvp-post-more-img left relative">
												<img width="400" height="240" src={article.Imagelink} class="mvp-reg-img wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-400x240.jpg 400w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-600x360.jpg 600w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-300x180.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-768x461.jpg 768w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-1024x614.jpg 1024w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-1000x600.jpg 1000w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-590x354.jpg 590w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics.jpg 1600w" sizes="(max-width: 400px) 100vw, 400px"/>
												<img width="80" height="80" src={article.Imagelink} class="mvp-mob-img wp-post-image" alt="" srcset="http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-80x80.jpg 80w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-300x300.jpg 300w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-100x100.jpg 100w, http://mvpthemes.com/zoxnews/wp-content/uploads/2017/07/politics-150x150.jpg 150w" sizes="(max-width: 80px) 100vw, 80px"/>
												<div class="mvp-vid-box-wrap mvp-vid-box-mid mvp-vid-marg">
												<i aria-hidden="true">{article.Newspaper}</i>
												</div>
											</div>
											<div class="mvp-post-more-text left relative">
												<div class="mvp-cat-date-wrap left relative">
													<span class="mvp-cd-cat left relative">{article.Category}</span>
													<span class="mvp-cd-date left relative">{age}</span>
												</div>
												<p>{article.Title}</p>
											</div>
										</li>
									</a>
									)
								})}

							</ul>
							</div>
				</div>
            ):(<div style={{marginTop:'350px', height:'700px'}}><center><CircularProgress disableShrink /></center></div>)
        }
		};
const mapStateToProps = (state, props) =>  {

	const articles = state.article.articles.filter(article => article.Category === this.props.match.params.category);
	const Trendarticle = articles.find(article => article.Place === 'top1');
	
	return {
		articles, Trendarticle
	}
}
export default connect(mapStateToProps, null)(Cat);